input[type="date"]:focus {
  border: 0;
  outline: none;
}

.is-negative {
  background-color: #ad161630;
  border: .1vw solid #7f1111;
}

.is-hidden {
  display: none;
}

.form__date {
  color: #8d8d8d;
  border: 1px #000;
  width: 100%;
  height: 100%;
  font-family: aktiv-grotesk, sans-serif;
  font-size: .8vw;
  font-weight: 600;
  line-height: 1vw;
}

.co-buyer__sibling {
  grid-column-gap: 2vw;
  grid-row-gap: 2vw;
  background-color: #fff;
  border: .1vw solid #d8d8d8;
  border-radius: 1vw;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  padding: 2vw;
  display: flex;
}

.co-buyer__wrapper {
  grid-column-gap: .5vw;
  grid-row-gap: .5vw;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  display: flex;
}

.co-buyer-employment-status__wrapper {
  width: 100%;
}

.car-image__wrapper {
  z-index: 999;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.car-image__wrapper img {
  object-fit: cover;
  border-radius: 1vw;
  width: auto;
  height: 19vw;
}

@media only screen and (width >= 1920px) {
  .car-image__wrapper img {
    border-radius: 20px;
    height: 360px;
  }

  .form__date {
    font-size: 15px;
    line-height: 19px;
  }

  .co-buyer__sibling {
    grid-column-gap: 40px;
    grid-row-gap: 40px;
    background-color: #fff;
    border: 2px solid #d8d8d8;
    border-radius: 20px;
    padding: 40px;
  }

  .co-buyer__wrapper {
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    width: 100%;
  }
}

@media only screen and (width <= 991px) {
  .car-image__wrapper img {
    border-radius: 1.5vw;
    height: 24vw;
  }

  .form__date {
    font-size: 1vw;
    line-height: 1.2vw;
  }

  .co-buyer__sibling {
    grid-column-gap: 2.5vw;
    grid-row-gap: 2.5vw;
    border: .2vw solid #d8d8d8;
    border-radius: 1.2vw;
    padding: 2.5vw;
  }

  .co-buyer__wrapper {
    grid-column-gap: .7vw;
    grid-row-gap: .7vw;
  }
}

@media only screen and (width <= 478px) {
  .car-image__wrapper img {
    border-radius: 3vw;
    height: 30vw;
  }

  .co-buyer__wrapper {
    grid-column-gap: 1vw;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 3.5vw 3vw;
  }

  .form__date {
    font-size: 2.5vw;
    line-height: 2.8vw;
  }

  .co-buyer__sibling {
    grid-column-gap: 0vw;
    grid-row-gap: 0vw;
    border: .2vw solid #d8d8d8;
    border-radius: 2vw;
    padding: 0;
  }
}

